<template>
	<view class="switcher">
		<view class="switcher__item" v-for="(item, index) of list" :key="index"
			:class="{ 'switcher__item-active': currentIndex === index }" @click="currentIndex = index;
      emit('update:modelValue', item);">
			{{ item }}
		</view>
	</view>
</template>

<script>
	export default {
		name: 'Switcher',
		props: {
			list: {
				type: Array,
				default: () => ['数据浏览', '数据申请'],
			},
			modelValue: {
				type: String,
				default: '数据浏览',
			},
		},
		emits: ['update:modelValue'],
		setup(props, {
			emit
		}) {
			const currentIndex = ref(0);

			return {
				currentIndex,
			};
		},
	};
</script>

<style lang="scss">
	.switcher {
		display: flex;
		flex-direction: row;
		justify-content: space-between;

		&__item {
			width: 64px;
			height: 20px;
			font-size: 12px;
			font-family: PingFang SC;
			font-weight: 500;
			color: #cfcfcf;
			opacity: 0.6;
			cursor: pointer;
			text-align: center;
			line-height: 21px;

			&-active {
				opacity: 1;
				background: url('../../static/icon-common-switcher.png');
				height: 20px;
				width: 64px;
			}
		}
	}
</style>
